import React from 'react';
import logo from './oncology_white.png';
import './_header.scss';
import { FaTwitter, FaLinkedin, FaYoutube, FaGoogle } from 'react-icons/fa'
function Header() {
    return (
        <div className="backgroundBlue">
            <div className="header">
                <img src={logo} className="App-logo" alt="logo" />
                <h1>OncoAid</h1>

                <div className="float-right social-icons d-none d-lg-block">
                    <FaGoogle></FaGoogle>
                    <FaLinkedin></FaLinkedin>
                    <FaTwitter></FaTwitter>
                    <FaYoutube></FaYoutube>
                </div>
            </div>
        </div>
    );
}

export default Header;